<template>
	<div class="shopbag">
		<van-nav-bar title="结账" fixed left-text="返回" @click-left="goRet" left-arrow>
			<template #right>
				<div @click="changePattern" v-if="shopbagAllData.length>0">{{isPattern?'编辑':'完成'}}</div>
			</template>
		</van-nav-bar>
		<div class="one" v-if="shopbagAllData.length>0">
			<img class="auto-img topImg" src="../assets/images/shopbag_bg.png" alt="">
			<div class="choiceBuy">
				<van-swipe-cell v-for="(item,index) in shopbagData" :key="item.sid" :disabled="!isPattern">
					<div class="buyList">
						<van-checkbox v-model="item.isChecked" label-disabled @change="simpleSelect"></van-checkbox>
						<van-card :price="item.price" :desc="item.rule" :title="item.name" class="goods-card" :thumb="item.small_img" />
						<van-stepper v-model="item.count" theme="round" @change="upDataShopbagCount(item.sid,item.count)" button-size="17"
						disable-input />
					</div>
					<template #right>
						<van-button square text="删除" @click="removeCom([item.sid]),index" type="danger" class="delete-button" />
					</template>
				</van-swipe-cell>

			</div>
			<span class="tips">哎呀,你买太少东西啦!</span>
			<!-- 结算按钮 -->
			<van-submit-bar v-show="isPattern" @submit="commit"
			:price="total" safe-area-inset-bottom button-text="提交订单" button-color="#21b276">
				<van-checkbox v-model="checked" @click="allSelect">全选</van-checkbox>
			</van-submit-bar>
			<!-- 删除按钮 -->
			<van-submit-bar v-show="!isPattern" @submit="removeSelect" safe-area-inset-bottom button-text="删除" button-color="#21b276">
				<van-checkbox v-model="checked" @click="allSelect">全选</van-checkbox>
			</van-submit-bar>
		</div>
		<div class="two" v-else>
			<van-empty description="您的购物车,空空如也...">
				<div class="goHome" @click="goHome">去瞅瞅...</div>
			</van-empty>
		</div>
	</div>
</template>

<script>
	import "../assets/less/Shopbag.less"
	import {
		mapState,
		mapMutations
	} from "vuex"
	export default {
		name: "Shopbag",
		data() {
			return {
				//当前是否全的状态
				checked: false,
				//当前的商品数量
				//切换编辑状态
				isPattern: true,
				//购物袋的所有数据
				shopbagAllData: [],
				//购物袋数据
				shopbagData: [],
				//总金额
				total:0


			}
		},
		created() {
			this.getAllShopcar();
			this.getShopbagCount()
		},
		computed: {
			...mapState(["shopbagCount"])
		},
		methods: {
			//返回首页
			goHome() {
				this.$router.push({
					name: "Main"
				})
			},
			...mapMutations(["changeShopbagCount","changeIsLoadShopbagCount"]),
			//获取购物袋提示数据
			getShopbagCount() {
				if (this.isLoadShopbagCount) {
					console.log("已经加载过购物袋数量");
					return;
				}
				//获取用户token值
				let tokenString = this.$cookies.get("tokenString");

				//如果token为空,说明用户没有登录或未注册
				if (!tokenString) {
					console.log("没有token值");
					return
				}
				this.$toast.loading({
					message: "加载中...",
					forbidClick: true,
					duration: 500,

				});
				//成功则请求
				this.axios({
					method: "GET",
					url: "/shopcartRows",
					params: {
						tokenString: tokenString
					}
				}).then((res) => {
					// console.log("res==>",res);
					// 清除登录账户密码
					this.$toast.clear();

					//如果没有登录
					if (res.data.code === 700) {
						return;
					}
					if (res.data.code === 8000) {
						//说明加入成功,改变购物袋显示数字
						this.changeShopbagCount(res.data.result);
						this.changeIsLoadShopbagCount(true);
					}
				}).catch((err) => {
					this.$toast.clear();
					console.log("err==>", err);
				})
			},
			changePattern() {
				this.isPattern = !this.isPattern
			},
			goRet() {
				this.$router.go(-1)
			},
			//获取购物车所有数据
			getAllShopcar() {
				//获取用户token值
				let tokenString = this.$cookies.get("tokenString");

				//如果token为空,说明用户没有登录或未注册
				if (!tokenString) {
					console.log("没有token值");
					return this.$router.push({name: "Login"})
				}
				this.$toast.loading({
					message: "加载中...",
					forbidClick: true,
					duration: 500,

				});
				//成功则请求
				this.axios({
					method: "GET",
					url: "/findAllShopcart",
					params: {
						tokenString: tokenString
					}
				}).then((res) => {
					// console.log("res==>",res);
					// 清除登录账户密码
					this.$toast.clear();

					// //如果没有登录
					if (res.data.code === 700) {
						return;
					}
					if (res.data.code === 5000) {
						res.data.result.forEach(v => {
							v.isChecked = false
						});
						this.shopbagAllData = res.data.result
						// console.log(this.shopbagAllData);
						this.shopbagData = this.shopbagAllData
					}

				}).catch((err) => {
					this.$toast.clear();
					console.log("err==>", err);
				})
			},
			//修改购物袋数量==========
			upDataShopbagCount(sid, count) {
				//获取用户token值
				let tokenString = this.$cookies.get("tokenString");

				//如果token为空,说明用户没有登录或未注册
				if (!tokenString) {
					console.log("没有token值");
					return this.$router.push({
						name: "Login"
					})
				}
				this.$toast.loading({
					message: "加载中...",
					forbidClick: true,
					duration: 500,

				});
				//成功则请求
				this.axios({
					method: "POST",
					url: "/modifyShopcartCount",
					data: {
						tokenString: tokenString,
						sid: sid,
						count: count
					}
				}).then((res) => {
					// console.log("res==>", res);
					// 清除登录账户密码
					this.$toast.clear();

					// //如果没有登录
					if (res.data.code === 700) {
						return;
					}
					if (res.data.code ===6000) {
						this.sum();
					}else{
						this.$toast.loading({
							message: "加载中...",
							forbidClick: true,
							duration: 500,
						
						});
					}

				}).catch((err) => {
					this.$toast.clear();
					console.log("err==>", err);
				})
			},
			//全选
			allSelect() {
				//将所有单选的状态改为全选状态
				this.sum()
				this.shopbagData.forEach(v => {
					v.isChecked = this.checked
				})
			},
			//反选
			simpleSelect() {
				this.sum()
				for (let i = 0; i < this.shopbagData.length; i++) {
					if (!this.shopbagData[i].isChecked) {
						this.checked = false;
						return;
					}
				}
				this.checked = true;
			},

			//删除单个商品商品
			removeCom(sids, index) {
				//获取用户token值
				let tokenString = this.$cookies.get("tokenString");

				//如果token为空,说明用户没有登录或未注册
				if (!tokenString) {
					console.log("没有token值");
					return this.$router.push({
						name: "Login"
					})
				}
				this.$toast.loading({
					message: "加载中...",
					forbidClick: true,
					duration: 500,

				});
				//成功则请求
				this.axios({
					method: "POST",
					url: "/removeShopcart",
					data: {
						tokenString: tokenString,
						sids: JSON.stringify(sids)
					}
				}).then((res) => {
					// console.log("res==>",res);
					// 清除登录账户密码
					this.$toast.clear();

					// //如果没有登录
					if (res.data.code === 700) {
						this.$toast({
							message: res.data.msg,
							forbidClick: true,
							duration: 500,
						});
					}
					if (res.data.code === 7000) {
						if (index != undefined) {
							this.shopbagData.splice(index, 1)
							this.changeShopbagCount(this.shopbagCount - 1);
							this.simpleSelect()
						} else {
							for (let i = 0; i < this.shopbagData.length; i++) {
								if (this.shopbagData[i].isChecked) {
									this.shopbagData.splice(i, 1);
									i--
								}
							}
							this.changeShopbagCount(this.shopbagCount - sids.length)
							this.sum()
						}

					}
					this.$toast({
						message: res.data.msg,
						forbidClick: true,
						duration: 500,
					});

				}).catch((err) => {
					this.$toast.clear();
					console.log("err==>", err);
				})
			},
			//删除选择的购物袋
			removeSelect() {
				//获取勾选的商品sid
				let sids = []
				this.shopbagData.forEach(v => {
					if (v.isChecked) {
						sids.push(v.sid);
					}
				});
				if (sids.length === 0) {
					this.$toast({
						message: "请选择删除项",
						forbidClick: true,
						duration: 500,
					});
					return
				}
				this.removeCom(sids)

				console.log(sids);
			},
			
			//勾选计算总金额
			sum(){
				let total=0;
				this.shopbagData.forEach(v=>{
					if (v.isChecked) {
						total+=v.price *v.count
					}
				})
				this.total=total*100;
			},
			
			//提交订单
			commit(){
				//获取到被勾选的商品的sid
				let sids=[];
				this.shopbagData.forEach((v)=>{
					if (v.isChecked) {
						sids.push(v.sid);
					}
				});
				if (sids.length===0) {
					this.$toast({
						message:"请选择需要的商品再提交订单哦!",
						forbidClick:true,
						duration:500
					});
					return;
				}
				this.$router.push({name:"OrderClosing",query:{sids:sids.join('-')}})
			}


		}
	}
</script>

